.panel {
    padding: 0 8px 10px 8px;
    width: 160px;
    position: absolute;
    top: 0;
    left: 0;
    text-emphasis: none;
    text-decoration: none;
    outline: none;
    user-select: none;
    -webkit-tap-highlight-color: transparent;
    touch-action: manipulation;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    transition: left 0.2s ease-in-out;
}
.panel > .logo {
    background-position: center center;
    background-repeat: no-repeat;
    width: 160px;
    height: 140px;
    margin: 0 0 16px 0;
    display: block;
}
.panel > .selection {
    margin: 8px 8px 2px 6px;
    background-color: #313233;
    border-right: 2px black solid;
    border-left: 2px black solid;
    border-top: 2px black solid;
}
.panel > .selection > .navigation {
    border-bottom: black 2px solid;
}
.panel > .selection > .navigation > .title {
    display: flex;
    align-items: center;
    color: #ffffff;
    padding: 6px 8px;
    font-size: 12px;
    font-weight: 400;
    text-decoration-line: none;
    text-decoration-style: solid;
    text-decoration-thickness: auto;
    text-emphasis: none;
    text-decoration: none;
    outline: none;
    user-select: none;
    -webkit-tap-highlight-color: transparent;
    touch-action: manipulation;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
}
.panel > .selection.close > .navigation > .title {
    border-top: #6d6d6e 2px solid;
    border-right: #5a5b5c 2px solid;
    border-bottom: #5a5b5c 2px solid;
    border-left: #6d6d6e 2px solid;
    background-color: #48494a;
    margin-bottom: 5px;
    height: 12px;
}
.panel > .selection.open > .navigation > .title {
    border-top: #5a5b5c 2px solid;
    border-right: #464747 2px solid;
    border-bottom: #464747 2px solid;
    border-left: #5a5b5c 2px solid;
    background-color: #313233;
    height: 17px;

}
.drop-icon {
    display: flex;
    width: 12px;
    height: 12px;
    align-items: center;
}
.selection[class*="open"] .drop-icon::after {
    content: "";
    display: block;
    background: 0 0;
    position: relative;
    width: 0;
    height: 0;
    border-style: solid;
    border-color: #AAA transparent;
    border-width: 6px 5px 0;
    margin-right: -11px;
    margin-bottom: 1px;
}
.selection[class*="close"] .drop-icon::after {
    content: "";
    display: block;
    background: 0 0;
    position: relative;
    width: 0;
    height: 0;
    border-style: solid;
    border-color: #AAA transparent;
    border-width: 6px 5px 0;
    margin-right: -11px;
    margin-bottom: 1px;
    -webkit-transform: rotate(-90deg);
    transform: rotate(-90deg);
}
.panel > .selection > .navigation > .list {
    background-color: #232324;
    border: 2px #313233 solid;
    padding: 4.8px 8px;
    display: flex;
    flex-direction: column;
}
.panel > .selection.close > .navigation:has(.list) {
    display: none;
}
.panel > .selection > .navigation > .list > .item {
    color: #bfbfbf;
    font-size: 12px;
    padding: 3px 0px;
    text-decoration: none;
}
#panel-c {
    position: fixed;
    width: 32px;
    height: 32px;
    top: 10px;
    left: 10px;
    border-top: #5a5b5c 2px solid;
    border-right: #464747 2px solid;
    border-bottom: #464747 2px solid;
    border-left: #5a5b5c 2px solid;
    background-color: #313233;
    color: white;
    align-items: center;
    justify-content: center;
    font-size: 20px;
    display: none;
}