.head {
    padding: 0;
    height: 90px;
    margin-left: 176px;
    display: flex;
    align-items: end;
    transition: margin-left 0.2s ease-in-out;
    position: relative;
    margin-right: 4px;
}

.head .menus {
    display: flex;
    justify-content: space-between;
    height: 36px;
    width: 100%;
}

.head .menus>.decoration {
    position: absolute;
    bottom: 0;
    width: calc(100% - 15px);
    height: 6px;
}

.head .menus>.decoration::after {
    content: "";
    bottom: 0;
    right: -6px;
    position: absolute;
    min-width: 6px;
    height: 6px;
}

.head .menus .menu {
    position: relative;
    display: flex;
    padding: 15px 6px 0 12px;
    margin: 0 12px 0 6px;
    z-index: 1;
    font-size: 13px;
    font-weight: 700;
    background-position: -6px 0;
    background-repeat: no-repeat;
    height: calc(100% - 15px);
    text-decoration: none;
}

.head .menus .menu::before {
    position: absolute;
    content: "";
    top: 0;
    left: -6px;
    width: 6px;
    height: 100%;
}

.head .menus .menu::after {
    position: absolute;
    display: block;
    content: "";
    top: 0;
    right: -6px;
    width: 6px;
    background-position: right top;
    height: 100%;
}

.head .menus .menu.active,
.head .menus .menu:hover {
    background-position: -6px bottom;
}

.head .menus .menu.active::after,
.head .menus .menu:hover::after {
    background-position: right bottom;
}

.head .menus .left {
    display: flex;
    flex-direction: row;
}