body .App {
  max-width: 100dvw;
  min-height: 100dvh;
  padding: 0;
  margin: 0;
  background-repeat: repeat-x, repeat;
  background-position: top left, top left;
  background-size: auto 234px, 468px;
}

.account {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 100;
  display: flex;
  margin: 8px 8px 2px 6px;
  background-color: #313233;
  border: 2px black solid;
}

.account > .selection {
  background-color: #232324;
  border: 2px #313233 solid;
  padding: 4.8px 8px;
  display: flex;
}

@media only screen and (max-width: 700px) {
  .panel {
    position: fixed;
    z-index: 3;
    height: 100%;
    left: -176px;
  }
  .panel.open {
    left: 0px;
    background-color: rgba(0, 0, 0, 0.75);
  }
  .content {
    margin-left: 8px;
  }
  .head {
    margin-left: 8px;
  }
  #panel-c {
    display: flex;
  }
  .language > .drop > .current {
    display: none;
  }
}