.content {
    margin: 0;
    display: flex;
    margin-left: 176px;
    margin-right: 10px;
    transition: margin-left 0.2s ease-in-out;
    position: relative;
    padding: 20px 24px 24px 24px;
    display: flex;
    flex-direction: column;
}

.content::after {
    content: "";
    position: absolute;
    width: 6px;
    height: calc(100% + 36px);
    top: -36px;
    left: 0px;
    background-repeat: no-repeat;

}

.content>.top {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
    height: 49.6px;
    border-bottom: 1px solid #a2a9b1;
}

.content>.top>.title {
    display: flex;
    align-items: center;
    background: transparent;
    font-size: 20px;
    font-weight: 600;
}

a {
    text-decoration: none !important;
    &:focus, &:hover, &:visited, &:link, &:active {
        text-decoration: none !important;
    }
}
.content>.top>.language > .drop > .current {
    margin-left: 10px;
    transition: width 0.2s ease-in-out;
}
.content>.top>.language>.drop {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0;
    padding: 10px;
    position: relative;
}

.content>.top>.language>.drop>.icon {
    width: 24px;
    height: 24px;
    margin: 0;
    background-size: 24px 24px;
    background-repeat: no-repeat;
    mask-image: url("../public/language.svg");
    mask-repeat: no-repeat;
    mask-position: center;
}

.content>.top>.language>.drop>.down {
    padding: 0;
    position: absolute;
    top: 100%;
    display: flex;
    flex-direction: column;
}
.content>.top>.language>.drop>.down>.lang {
    padding: 5px 6px;
    font-size: 14px;
    font-weight: 700;
    cursor: pointer;
}
.content table {
    margin: 1em 0;
    border-collapse: collapse;
}

.content tbody, .content thead, .content tr, .content td, .content th {
    border-collapse: collapse;
}
.content td, .content th {
    padding: 2.8px 5.6px;
}